import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { spacing, borders, sizing } from '@material-ui/system';
import { Box, styled } from '@material-ui/core';
import Image from 'gatsby-image';
import { Body1 } from './atoms';

const Img = styled(Image)(spacing, borders, sizing);

const Avatar = ({ fuild, name, gatsbyImageProps, ...rest }) => (
  <Box width="70px" height="70px" {...rest}>
    <Img mb={0} imgStyle={{ borderRadius: '50%' }} {...gatsbyImageProps} />
  </Box>
);

const Bio = (props) => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
        publicURL
        childImageSharp {
          fluid(maxWidth: 500) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
            summary
          }
        }
      }
    }
  `);

  const { author } = data.site.siteMetadata;
  return (
    <Box display="flex" alignItems="center" {...props}>
      {/* <Avatar
        gatsbyImageProps={{
          fluid: data.avatar.childImageSharp.fluid,
          alt: author.name,
        }}
      /> */}
      <Body1 ml={2}>
        <span role="img" aria-label="writting">
          ✍
        </span>
        ️ by <strong>{author.name}</strong> - {author.summary}
      </Body1>
    </Box>
  );
};

export default Bio;
